function bidirectional_dijkstra( startNode, endNode ){
    let graph = new Array(25);
    for( let i=0;i<25;i++ ){
        graph[i] = new Array(61);
        for( let j=0;j<61;j++ ){
            graph[i][j] = 0;
        }
    }

    let graphR = new Array(25);
    for( let i=0;i<25;i++ ){
        graphR[i] = new Array(61);
        for( let j=0;j<61;j++ ){
            graphR[i][j] = 0;
        }
    }

    let qStart = [];
    let visitedStart = [];

    let qEnd = [];
    let visitedEnd = [];

    let finalVisited = [];
    let finalPath = [];

    qStart.push(startNode);
    qEnd.push(endNode);
    while( qStart.length > 0 && qEnd.length>0){
        let minPosStart = 0;
        for( let i=0;i<qStart.length;i++ ){
            if( qStart[i].score < qStart[minPosStart].score ){
                minPosStart = i;
            }
        }

        let currentStart = qStart[minPosStart];
        if( visitedEnd.includes(currentStart) ){
            console.log("Done GRAPH Done !!!", startNode, endNode, currentStart);
            console.log(graph);

            console.log("This is 2nd query!!! (graphR)");
            console.log(graphR);

            //Start
            let temp = currentStart;
            let tempPath = [];
            tempPath.push(temp);
            while( graph[temp.x][temp.y] ){
                tempPath.push(graph[temp.x][temp.y]);
                temp = graph[temp.x][temp.y];
            }

            finalPath.push(startNode);
            for( let i=tempPath.length-1;i>=0;i-- ){
                finalPath.push(tempPath[i]);
            }

            //End
            temp = currentStart;
            finalPath.push(temp);
            while( graphR[temp.x][temp.y] ){
                finalPath.push(graphR[temp.x][temp.y]);
                temp = graphR[temp.x][temp.y];
            }

            console.log("FinalPath:>> ", finalPath);
            return {finalPath, finalVisited};
        }

        qStart = qStart.filter(elt => elt!==currentStart);
        visitedStart.push(currentStart);
        finalVisited.push(currentStart);
        // graph[currentStart.x][currentStart.y] = 1;

        for( let nbPos = 0; nbPos < currentStart.neighbors.length ; nbPos++ ){
            let neighbor = currentStart.neighbors[nbPos];

            if( document.getElementById(`node-${neighbor.x}-${neighbor.y}`).className == 'node-wall' ){
                continue;
            }

            if( !visitedStart.includes(neighbor) ){
                let temp_gScore = currentStart.gScore + 1;
                let newPath = false;
                if( qStart.includes(neighbor) ){
                    if( temp_gScore < neighbor.gScore ){
                        neighbor.gScore = temp_gScore;
                        newPath = true;
                    }
                }
                else{
                    neighbor.gScore = temp_gScore;
                    newPath = true;
                    qStart.push(neighbor);
                }

                if( newPath ){
                    neighbor.hScore = heruistic(neighbor, endNode);
                    neighbor.score = neighbor.gScore + neighbor.hScore;
                    neighbor.previous = currentStart;
                    graph[neighbor.x][neighbor.y] = currentStart;
                }
            }
        }


///////////////////////////////////////////////////////////////////////////////////////////////////////
        let minPosEnd = 0;
        for( let i=0;i<qEnd.length;i++ ){
            if( qEnd[i].score < qEnd[minPosEnd].score ){
                minPosEnd = i;
            }
        }

        let currentEnd = qEnd[minPosEnd];
        if( visitedStart.includes(currentEnd) ){
            console.log("Done GRAPHr Done !!!", startNode, endNode, currentEnd);
            console.log(graphR);

            console.log("This is 2nd query!!! (graph)");
            console.log(graph);

            //Start
            let temp = currentEnd;
            let tempPath = [];
            tempPath.push(temp);
            while( graph[temp.x][temp.y] ){
                tempPath.push(graph[temp.x][temp.y]);
                temp = graph[temp.x][temp.y];
            }

            finalPath.push(startNode);
            for( let i=tempPath.length-1;i>=0;i-- ){
                finalPath.push(tempPath[i]);
            }
            
            //End
            temp = currentEnd;
            finalPath.push(temp);
            while( graphR[temp.x][temp.y] ){
                finalPath.push(graphR[temp.x][temp.y]);
                temp = graphR[temp.x][temp.y];
            }

            console.log("FinalPath:>> ", finalPath);
            return {finalPath, finalVisited};
        }

        qEnd = qEnd.filter(elt => elt!==currentEnd);
        visitedEnd.push(currentEnd);
        finalVisited.push(currentEnd);
        // graph[currentStart.x][currentStart.y] = 1;

        for( let nbPos = currentEnd.neighbors.length - 1; nbPos >= 0  ; nbPos-- ){
            let neighbor = currentEnd.neighbors[nbPos];

            if( document.getElementById(`node-${neighbor.x}-${neighbor.y}`).className == 'node-wall' ){
                continue;
            }

            if( !visitedEnd.includes(neighbor) ){
                let temp_gScore = currentEnd.gScore + 1;
                let newPath = false;
                if( qEnd.includes(neighbor) ){
                    if( temp_gScore < neighbor.gScore ){
                        neighbor.gScore = temp_gScore;
                        newPath = true;
                    }
                }
                else{
                    neighbor.gScore = temp_gScore;
                    newPath = true;
                    qEnd.push(neighbor);
                }

                if( newPath ){
                    neighbor.hScore = heruistic(neighbor, startNode);
                    neighbor.score = neighbor.gScore + neighbor.hScore;
                    neighbor.previous = currentEnd;
                    graphR[neighbor.x][neighbor.y] = currentEnd;
                }
            }
        }
    }

    return {finalPath, finalVisited, error:'No Path Found!!!'};
}

function heruistic( start, end ){
    return 0;
}

export default bidirectional_dijkstra;
