function honeyCombMaze( startNode, endNode, rows, cols ){
    let walls = [];
    let nRows = 10;
    let nCols = 20;

    //Marking the boudries as walls
    for( let i=0;i<rows;i++ ){
        walls.push( [i, 0] );

        walls.push( [i, cols-1] );
    }

    //Marking the boudries as walls
    for( let j=0;j<cols;j++ ){
        walls.push( [0, j] );

        walls.push( [rows-1, j] );
    }

    for( let i=3;i<100;i+=3 ){
        let x = i;
        let y = 0;
        while( x > 0 ){
            if( ((x>0)&&(x<rows-1)) && ((y>0)&&(y<cols-1)) ){
                if( (Math.random() > 0.85) && ((x>(rows/nRows))&&(x<((nRows-1)*rows/nRows))) && ((y>(cols/nCols)))&&(y<((nCols-1)*cols/nCols))){
                    //This is just to skip the wall tile
                }
                else{
                    walls.push([x, y]);
                }
            }
            x-=1;
            y+=1;
        }
    }

    return walls;
}

export default honeyCombMaze;
